.Sidebar {
  float: left;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex: 1 1;
      flex: 1 1;
  width: 20%;
  min-height: 100vh;
  height: calc(100% - 1000px);    
  background: #4d4d4d;
  color: white;
}

.Sidebar > * {
  border-bottom: 2px solid gray;
  width: 100%;
}

hr {
  width: 100%;
}


